<template>
	<view class="body">
		<view class="content">
			<view class="head">
				<view class="true">正确</view>
				<view class="false">错误</view>
				<view class="notSet">未估分</view>
			</view>
			<view class="list">
				<view :class="{cell:true, true:item.score_log>0, false:item.score_log==0}" v-for="(item,index) in data.data" :key="index" @click="toDetails(index)">{{index+1}}</view>
			</view>
			<view class="submit" @click="report">提交并查看分数</view>
		</view>
		<!-- 弹窗模块 -->
		<view class="popUpMoudle" v-if="show" @click="show=false">
			<view class="modle">
				<image class="trophy" src="../../static/assessment/trophy_icon.png" mode="widthFix"></image>
				<view class="text">同学加油，祝您顺利上岸！</view>
				<view class="info">
					<view class="title">- {{info.subject}} -</view>
					<view class="numModle">
						<image class="score" src="../../static/assessment/score_icon.png" mode="widthFix"></image>
						<view class="num">{{info.total_score}}</view>
					</view>
					<view class="sureButton" @click="confirm">确定</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { question, submitAnswer} from '@/common/http.api.js';
	export default {
		data() {
			return {
				show:false, //显示结果
				token:'',
				subject_id:'',
				isLast:0, //是否最后一题进入
				data:{},
				notDoAll:false, //没有做完所有题
				info:{}
			}
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			this.subject_id = option.subject_id
			this.isLast = option.last
			question({token:this.token, subject_id:option.subject_id}).then(res=>{
				console.log(res)
				this.data = res.data.data	
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			confirm() {
				this.show = false
				uni.redirectTo({
					url:'/pages/assessment/assessmentStepTwo'
				})
			},
			report() {
				if(this.isLast==1){
					submitAnswer({token:this.token, subject_id:this.subject_id}).then(res=>{
						console.log(res)
						if(res.data.code==-1){
							this.$u.toast(res.data.data)
						}else{
							this.info = res.data.data
							this.show = true
						}
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast('没有做完所有题')
				}
			},
			// 去题
			toDetails(e) {
				uni.navigateBack({
					delta:1
				})
				uni.setStorage({
					key: 'subject_id',
					data: this.subject_id,
					success: function () {
						console.log('success');
					}
				});
				uni.setStorage({
					key: 'current',
					data: e,
					success: function () {
						console.log('success');
					}
				});
				uni.setStorage({
					key: 'isAnswerCard',
					data: 1,
					success: function () {
						console.log('success');
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 30rpx;
			background-color: #fff;
			.head{
				display: flex;
				font-size: 28rpx;
				padding-bottom: 20rpx;
				margin-bottom: 20rpx;
				border-bottom: 1rpx solid #eee;
				.true{
					display: flex;
					align-items: center;
					margin-right: 20rpx;
				}
				.true::before{
					content: '';
					display: block;
					background-color: #3fc296;
					width: 20rpx;
					height: 20rpx;
					border-radius: 50%;
					margin-right: 10rpx;
				}
				.false{
					display: flex;
					align-items: center;
					margin-right: 20rpx;
				}
				.false::before{
					content: '';
					display: block;
					background-color: #ff5454;
					width: 20rpx;
					height: 20rpx;
					border-radius: 50%;
					margin-right: 10rpx;
				}
				.notSet{
					display: flex;
					align-items: center;
					margin-right: 20rpx;
				}
				.notSet::before{
					content: '';
					display: block;
					background-color: #d7d7d7;
					width: 20rpx;
					height: 20rpx;
					border-radius: 50%;
					margin-right: 10rpx;
				}
			}
			.list{
				display: flex;
				flex-wrap: wrap;
				.cell{
					width: 70rpx;
					font-size: 28rpx;
					text-align: center;
					line-height: 70rpx;
					border-radius: 50%;
					background-color: #d6d6d6;
					color: #fff;
					margin-bottom: 30rpx;
					margin-right: 38rpx;
				}
				.true{
					background-color: #3fc296 !important;
				}
				.false{
					background-color: #ff5454 !important;
				}
				.cell:nth-child(6n){
					margin-right: 0;
				}
			}
			.submit{
				line-height: 78rpx;
				background-color: #ffd656;
				color: #fff;
				text-align: center;
				border-radius: 50rpx;
				margin-top: 50rpx;
			}
		}
		.popUpMoudle{
			width: 100vw;
			height: 100vh;
			z-index: 999;
			position: fixed;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, .3);
			display: flex;
			justify-content: center;
			align-items: center;
			.modle{
				width: 70%;
				border-radius: 20rpx;
				position: relative;
				background: linear-gradient(to bottom,#79a5ff 0%,#a6ccff 50%);
				.trophy{
					width: 300rpx;
					position: absolute;
					top: -10rpx;
					left: 50%;
					right: 50%;
					transform: translate(-50%,-50%);
				}
				.text{
					color: #fff;
					margin-top: 120rpx;
					margin-bottom: 50rpx;
					text-align: center;
					font-size: 30rpx;
					font-weight: 500;
				}
				.info{
					background-color: #fff;
					border-bottom-left-radius: 20rpx;
					border-bottom-right-radius: 20rpx;
					padding: 20rpx;
					.title{
						text-align: center;
						font-size: 30rpx;
					}
					.numModle{
						background-color: #f5f7fa;
						border-radius: 16rpx;
						margin: 40rpx 30rpx;
						padding: 20rpx;
						padding-top: 40rpx;
						position: relative;
						.score{
							width: 160rpx;
							position: absolute;
							top: 10rpx;
							left: 50%;
							right: 50%;
							transform: translate(-50%,-50%);
						}
						.num{
							font-size: 68rpx;
							font-weight: 700;
							text-align: center;
						}
					}
					.sureButton{
						width: 250rpx;
						line-height: 78rpx;
						font-size: 34rpx;
						font-weight: 500;
						background-color: #81abff;
						color: #fff;
						border-radius: 50rpx;
						margin: 0 auto;
						text-align: center;
					}
				}
			}
		}
	}
</style>